.popover {
    width: 250px;
    position: absolute;
    border: gray 1px solid;
    border-radius: 5px;
}

.arrow {
    background-color: white;
    border-bottom: 1px solid gray;
    border-left: 1px solid gray;
    bottom: -1px;
    display: inline-block;
    height: 12px;
    left: 120px;
    position: absolute;
    transform: matrix(0.7071, -0.7071, 0.7071, 0.7071, -2, 6);
    width: 12px;
    z-index: 2;
}

.content {
    padding: 5px;
}

h2 {
    background-color: rgb(236, 236, 234);
    margin: 0;
    border-bottom: 1px solid gray;
    padding: 5px;
    font-weight: 200;
}